import React from "react";
import { Container, Row, Col, OverlayTrigger, Tooltip } from "react-bootstrap";
import { Link } from "react-router-dom";
import Pure from "./Pure";
import client from "./CLASSES/client";
import Building from "./CLASSES/buildings";
import ImageMap from "./Images";
import "./Village.css";
import sharedMemory from "./CLASSES/SharedMemory";

class Village extends Pure {
    title = React.createRef();
    updateTitle(title = "") { this.title.current.textContent = title; }
    render() {
        return <div className="VillageView">
            <h4 className="VillageName title" ref={this.title}>{client.activeVillageName}</h4>
            <Container>
                <Row>
                    <Col>
                        <div>
                            <svg viewBox="0 0 300 264" className="feilds">
                                <VillageView register="villageView" />
                            </svg>
                        </div>
                    </Col>
                    <Col>
                        <div className="movements"></div>
                        <div className="productions"></div>
                        <div className="troops"></div>
                    </Col>
                </Row>
            </Container>
        </div>;
    }
}

class VillageView extends Pure {
    name = "";
    state = { Content: [] }
    onReady() {
        client.village(village => {
            if (!village) return;
            sharedMemory.safe("village", v => v.updateTitle(village.name));
            let Content = [], vId = village.id, Build = village.buildings;
            Content.push(<image key="FeildsBackground" xlinkHref={ImageMap.village[village.type]} />);
            for (let i = 1; i <= 18; i++)
                Content.push(Building.FromResponse(Build[i - 1], vId).LinkComponent);
            Content.push(<Link key={"Center-Building"} to={"/Center"}>
                <OverlayTrigger placement="top" overlay={<Tooltip id={"center-tooltip"}>مركز القرية</Tooltip>}>
                    <path opacity={0} d="M122 108 148 105 169 108 182 120 184 133 181 143 170 152 160 155 141 157 130 157 114 153 106 145 102 134 104 124 110 115Z" />
                </OverlayTrigger>
            </Link>);
            this.updateState({ Content });
        });
    }
    render() {
        return this.state.Content;
    }
}

export default Village;